import { types } from 'mobx-state-tree'

//    - (message),   (nickname),    (isSelf)  
const model = types
  .model('messageModel', {
    message: types.string,
    nickName: types.string,
    nickId: types.string,
    isSelf: types.boolean
  });


const defaultValue = {
  message: '',
  nickName: '',
  nickId: '',
  isSelf: false
};


const create = model.create(defaultValue);


const messageStore = {
  model,
  defaultValue,
  create
};


export default messageStore;
  

